






























import { Component, Vue, Prop, Watch } from "vue-property-decorator";
import Bird from "@/modules/game/Bird";
@Component
export default class BirdDialog extends Vue {
  @Prop(Boolean)
  public value: Boolean = false;
  @Watch("value")
  private valueChange(val: Boolean) {
    if (val) {
      this.step = 1;
    }
    this.visible = val;
  }
  @Watch("visible")
  private visibleChange(val: Boolean) {
    this.$emit("input", val);
  }
  public visible: Boolean = false;
  public step: number = 1;
  public maxStep: number = 2;
  public epochs: number = 10;
  public drawerVisible: Boolean = true;
  public unitForm: any = {};
  public prev() {
    this.step--;
  }
  public next() {
    this.step++;
  }
  public complete() {
    this.visible = false;
    this.$emit(
      "on-bird-create",
      new Bird({
        epochs: this.epochs,
        modelOptions: [{ useBias: true, units: 4 }]
      })
    );
  }
}
